<script lang="ts">// @ts-check
export let value;
export let oninput;
const handleInput = (event) => {
    const value = event.target.value;
    oninput(value);
};
</script>

<input type="color" {value} on:input={handleInput} />

<style>
  input[type="color"] {
    position: absolute;
    padding: 0;
    margin: -0.3em 0;
    right: 0.4em;
    border: 0;
    width: 3em;
    cursor: pointer;
  }
</style>
