<script lang="ts">// @ts-check
export let value;
export let oninput;
export let min = 0;
export let max = Infinity;
export let step = 1;
const handleInput = (event) => {
    const value = event.target.value;
    oninput(+value);
};
</script>

<input type="number" {min} {max} {step} {value} on:input={handleInput} />

<style>
  input[type="number"] {
    position: absolute;
    padding: 2px;
    margin: -0.25em 0;
    right: 0.4em;
    width: 3em;
    border: 0;
  }
</style>
