<script lang="ts">// @ts-check
import { tooltip } from "scripts/tooltip";
import { iconedNav } from "data/stores";
export let value = undefined;
export let label = undefined;
export let tip = undefined;
export let gesture = undefined;
export let hotkey = undefined;
export let disabled = false;
export let selected = false;
export let flutter = false;
export let right = false;
export let onclick;
const handleClick = (event) => {
    event.stopPropagation();
    onclick();
};
</script>

<li class:disabled class:selected class:flutter class:right data-tooltip={tip} data-gesture={gesture} data-hotkey={hotkey} on:click={handleClick} use:tooltip>
  {#if value && $iconedNav}
    <svg>
      <use href="#{value}-icon" />
    </svg>
  {:else if label}
    {label}
  {/if}

  <slot />
</li>

<style>
  li {
    user-select: none;
    padding: 1em;
    list-style: none;
    transition: all 0.1s;
  }

  li:hover {
    background-color: #2d2e2f;
  }

  li:active:not(:last-child) {
    transform: translateY(1px);
  }

  li:not(.disabled) {
    color: #fff;
    cursor: pointer;
  }

  li.disabled {
    pointer-events: none;
    color: #333;
  }

  li.selected:before {
    content: "\2713";
    display: inline-block;
    padding: 0 6px 0 0;
  }

  li.right {
    margin-left: auto;
  }

  svg {
    fill: currentColor;
    stroke: none;
    width: 1em;
    height: 1em;
    vertical-align: middle;
  }
</style>
