<script lang="ts">// @ts-check
import { tooltip } from "scripts/tooltip";
export let icon;
export let tip = undefined;
export let onclick;
const handleClick = (event) => {
    event.stopPropagation();
    onclick();
};
</script>

<svg on:click={handleClick} data-tooltip={tip} use:tooltip>
  <use href="#{icon}-icon" />
</svg>

<style>
  svg {
    fill: currentColor;
    width: 0.8em;
    height: 0.8em;
    vertical-align: middle;
    cursor: pointer;
  }

  svg:active {
    transform: translateY(1px);
  }
</style>
